import { Badge, Col, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { SettingService } from "../../../../services/taskService";
import { Box } from "../../../core/MyBox";
import { CheckCircleTwoTone } from '@ant-design/icons';
import { MyButton } from "../../../core/MyButton";
import ModifyStatusSettingComponent from "./ModifyStatusSettingComponent";

export default function StatusComponent(props: any) {
    const projectKey = useParams().key;
    const [dataSource, setDataSource] = useState([]);
    const [visible, setVisible] = useState(false);
    const [keyEdit, setKeyEdit] = useState('');
    const [visibleEdit, setVisibleEdit] = useState(false);

    const { setIsLoading } = props;
    const loadData = useCallback(() => {
        setIsLoading(true);
        SettingService.GetListSettingStatus(projectKey, (data: any) => {
            setDataSource(data.data.data);
        }, undefined, () => {
            setIsLoading(false);
        })
    }, [projectKey, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleClickAdd() {
        setVisible(true);
    }

    function handleClickDetail(key: string) {
        setKeyEdit(key);
        setVisibleEdit(true);
    }

    function RenderType(type: number) {
        let valueType = "";
        let colorType = "";
        switch (type) {
            case 1:
                valueType = "Trạng thái dự án";
                colorType = "blue"
                break;
            case 2:
                valueType = "Trạng thái sprint";
                colorType = "red"
                break;
            case 3:
                valueType = "Trạng thái công việc";
                colorType = "yellow"
                break;
            case 4:
                valueType = "Trạng thái vấn đề";
                colorType = "green"
                break;

            default:
                break;
        }

        return (
            <Badge color={colorType} text={valueType} />
        );
    }
    function RenderData() {
        return dataSource.map((data: any, index: number) => {
            return (
                <Col span={4} key={index} onClick={() => {
                    handleClickDetail(data.key);
                }}>
                    <Box>
                        <p>Value: {data.value}</p>
                        <p>Loại: {RenderType(data.type)}</p>
                        <p>Trạng thái mới: {data.isNew ? <CheckCircleTwoTone twoToneColor="#52c41a" /> : ""} </p>
                        <p>Trạng thái đóng: {data.isClosed ? <CheckCircleTwoTone twoToneColor="#52c41a" /> : ""}</p>
                    </Box>
                </Col>
            );
        });
    }
    return (
        <Box title="Quản lý trạng thái">
            <MyButton type="primary" value="Thêm trạng thái" onClick={handleClickAdd} />
            <Row gutter={12}>
                {RenderData()}
            </Row>
            <ModifyStatusSettingComponent
                title="Thêm mới trạng thái"
                projectKey={projectKey}
                visible={visible}
                setVisible={setVisible}
                loadData={loadData}
                isLoading={props.isLoading}
                setIsLoading={props.setIsLoading} />

            <ModifyStatusSettingComponent
                title="Sửa đổi trạng thái"
                keyData={keyEdit}
                projectKey={projectKey}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                loadData={loadData}
                isLoading={props.isLoading}
                setIsLoading={props.setIsLoading} />
        </Box>
    )
}