import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../../helper/function";
import { SettingService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { MyButton } from "../../../core/MyButton";

export default function ModifyStatusSettingComponent(props: any) {
    const keyData = props.keyData;
    const projectKey = props.projectKey;

    const [value, setValue] = useState('');
    const [type, setType] = useState('');
    const [isNew, setIsNew] = useState(false);
    const [isClosed, setIsClosed] = useState(false);
    const options = [
        {
            key: "1",
            label: "Trạng thái dự án",
            value: 1
        },
        {
            key: "2",
            label: "Trạng thái sprint",
            value: 2
        },
        {
            key: "3",
            label: "Trạng thái công việc",
            value: 3
        },
        {
            key: "4",
            label: "Trạng thái vấn đề",
            value: 4
        }
    ]

    let loadData = useCallback(() => {
        if (keyData !== undefined) {
            SettingService.GetDetailSettingStatus(projectKey, keyData, (data: any) => {
                console.log(data);
                const row = data.data.data;
                setValue(row.value);
                setType(row.type);
                setIsNew(row.isNew);
                setIsClosed(row.isClosed);
            })
        }
    }, [keyData, projectKey]);

    useEffect(() => {
        loadData();
    }, [loadData])

    function HandleCloseDrawer() {
        props.setVisible(false);
    }

    function handleSubmitForm() {
        let body = {
            value: value,
            type: type,
            isNew: isNew,
            isClosed: isClosed
        };

        props.setIsLoading(true);
        if (props.keyData === undefined) {
            Add(body);
        } else {
            Edit(body);
        }
    }

    function Add(body: any) {
        SettingService.AddSettingStatus(projectKey, body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            props.loadData();
            HandleCloseDrawer();
        }, () => {
            props.setIsLoading(false);
        });
    }

    function Edit(body: any) {
        SettingService.GetDetailSettingStatus(projectKey, keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            SettingService.EditSettingStatus(projectKey, keyData, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    function HandleClickDelete() {
       Swal.fire({
        title: "Bạn có muốn xóa không",
        text: "Bạn có muốn xóa không",
        showConfirmButton: true,
        showDenyButton: true,
       }).then((result: any) => {
        if (result.isConfirmed) {
            Delete();
        }
    })
    }

    function Delete() {
        props.setIsLoading(true);
        SettingService.GetDetailSettingStatus(projectKey, keyData, (data: any) => {
            SettingService.DeleteSettingStatus(projectKey, keyData, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
                HandleCloseDrawer();
            }, () => {
                props.setIsLoading(false);
            })
        }, () => {
            props.setIsLoading(false);
        })
    }

    return (
        <Drawer
            title={props.title}
            open={props.visible}
            onClose={HandleCloseDrawer}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Giá trị"
                    value={value}
                    setValue={setValue} />

                <MyFormItem.Select
                    label="Loại trạng thái"
                    options={options}
                    value={type}
                    setValue={setType} />

                <MyFormItem.Switch
                    label="Trạng thái mới"
                    value={isNew}
                    setValue={setIsNew} />

                <MyFormItem.Switch
                    label="Trạng thái đóng"
                    value={isClosed}
                    setValue={setIsClosed} />

                <MyButton
                    type="success"
                    htmlType="submit"
                    isLoading={props.isLoading}
                    value={props.keyData !== undefined ? "Sửa" : "Thêm"} />

                <MyButton
                    type="danger"
                    value="Xóa"
                    isLoading={props.isLoading}
                    onClick={HandleClickDelete}/>
            </Form>
        </Drawer>
    );
}