import { Drawer, Form } from "antd";
import { useCallback, useEffect, useState } from "react";
import Swal from "sweetalert2";
import { Fn } from "../../../../helper/function";
import { SettingService } from "../../../../services/taskService";
import { MyFormItem } from "../../../core/Input";
import { MyButton } from "../../../core/MyButton";

export default function ModifyOtherSettingComponent(props: any) {
    const [value, setValue] = useState('');

    const { setIsLoading } = props;

    const loadData = useCallback(() => {
        if (props.keyData !== undefined) {
            setIsLoading(true);
            SettingService.GetDetailSettingOther(props.projectKey, props.keyData, (data: any) => {
                setValue(data.data.data.value)
            }, undefined, () => {
                setIsLoading(false);
            })
        }
    }, [props.projectKey, props.keyData, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function onClose() {
        props.setVisible(false);
    }

    function handleSubmitForm() {
        var body = {
            value: value,
            type: props.type
        };
        if (props.keyData !== undefined) {
            Edit(body);
        } else {
            Add(body);
        }
    }

    function Add(body: any) {
        SettingService.AddSettingOther(props.projectKey, body, (data: any) => {
            Fn.PrintMsgSuccess(data);
            props.loadData();
        }, () => {
            setIsLoading();
        })
    }

    function Edit(body: any) {
        setIsLoading(true);
        SettingService.GetDetailSettingOther(props.projectKey, props.keyData, (data: any) => {
            body = Fn.GetTimestamp(data, body);
            SettingService.EditSettingOther(props.projectKey, props.keyData, body, (data: any) => {
                Fn.PrintMsgSuccess(data);
                props.loadData();
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    function handleClickDelete() {
        Swal.fire({
            title: "Xác nhận xóa",
            text: 'Bạn có muốn xóa không?',
        }).then((confirm) => {
            if (confirm.isConfirmed) {
                Delete();
            }
        })
    }

    function Delete() {
        setIsLoading(true);
        SettingService.GetDetailSettingOther(props.projectKey, props.keyData, (data: any) => {
            SettingService.DeleteSettingOther(props.projectKey, props.keyData, Fn.GetTimestamp(data), (data: any) => {
                Fn.PrintMsgSuccess(data);
                onClose();
                props.loadData();
            }, () => {
                setIsLoading(false);
            })
        }, () => {
            setIsLoading(false);
        })
    }

    return (
        <Drawer
            open={props.visible}
            onClose={onClose}
            title={props.keyData !== undefined ? "Chỉnh sửa" : "Thêm mới"}>
            <Form
                layout="vertical"
                onSubmitCapture={handleSubmitForm}>
                <MyFormItem.Input
                    label="Giá trị"
                    value={value}
                    setValue={setValue} />
                <MyButton
                    htmlType="submit"
                    isLoading={props.isLoading}
                    type="success"
                    value={props.keyData !== undefined ? "Chỉnh sửa" : "Thêm mới"} />
                <MyButton
                    type="danger"
                    isLoading={props.isLoading}
                    onClick={handleClickDelete}
                    value="Xóa" />
            </Form>
        </Drawer>
    );
}