import { Col, Row } from "antd";
import { useCallback, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { SettingService } from "../../../../services/taskService";
import { Box } from "../../../core/MyBox";
import { MyButton } from "../../../core/MyButton";
import ModifyOtherSettingComponent from "./ModifyOtherSettingComponent";

export default function OtherComponent(props: any) {
    let type = props.type;
    const projectKey = useParams().key;
    const [dataSource, setDataSource] = useState([]);
    const [visible, setVisible] = useState(false);
    const [visibleEdit, setVisibleEdit] = useState(false);
    const [keyEdit, setKeyEidt] = useState<string | undefined>(undefined);

    const { setIsLoading } = props;

    let loadData = useCallback(() => {
        setIsLoading(true);
        SettingService.GetListSettingOther(projectKey, type, (data: any) => {
            setDataSource(data.data.data);
        }, undefined, () => {
            setIsLoading(false);
        })
    }, [projectKey, type, setIsLoading]);

    useEffect(() => {
        loadData();
    }, [loadData]);

    function handleClickAdd() {
        setVisible(true);
    }

    function handleClickEdit(key: string | undefined) {
        setVisibleEdit(true);
        setKeyEidt(key);
    }

    function RenderData() {
        return dataSource.map((data: any, index: number) => {
            return (
                <Box key={index}>
                    <Row gutter={12}>
                        <Col span={12}>
                            {data.value}
                        </Col>
                        <Col span={12} style={{ textAlign: "right" }}>
                            <MyButton onClick={() => {
                                handleClickEdit(data.key)
                            }} value="Chỉnh sửa" />
                        </Col>
                    </Row>
                </Box>
            )
        })
    }

    return (
        <Box>
            <MyButton
                type="primary"
                value="Thêm mới"
                onClick={handleClickAdd} />
            {RenderData()}

            <ModifyOtherSettingComponent
                projectKey={projectKey}
                type={type}
                visible={visible}
                setVisible={setVisible}
                isLoading={props.isLoading}
                setIsLoading={props.setIsLoading}
                loadData={loadData} />

            <ModifyOtherSettingComponent
                projectKey={projectKey}
                type={type}
                visible={visibleEdit}
                setVisible={setVisibleEdit}
                isLoading={props.isLoading}
                setIsLoading={props.setIsLoading}
                keyData={keyEdit}
                loadData={loadData} />
        </Box>
    );
}